.App {
  text-align: center;
}

h1 {
  font-size: 3em;
  margin: 1em 0 0.5em 0;
}

h2 {
  font-size: 2em;
  margin: 1em 0 0.5em 0;
}

#editor, #preview {
  width: 100%;
  max-width: 700px;
  font-size: 1.4em;
  border-radius: 5px;
  padding: 1em;
}

#editor {
  font-family: 'Roboto Mono', monospace;
  height: 12em;
  background-color: #403d39;
  color: #fffcf2;
  border: none;
}

#preview {
  background-color: #fffcf2;  
  margin:auto;
  text-align: left;
}

#preview code {
  font-family: 'Roboto Mono', monospace;
}

#preview p, #preview pre, #preview ul {
  margin-bottom: 0.7em;
}

#preview li {
  margin-left: 2em;
}

#preview blockquote {
  padding-left: 0.5em;
  border-left: 5px solid #eb5e28;
  margin-left: 1em;
  font-style: italic;
}

#preview a {
  color: #eb5e28;
}

#preview h1 {
  font-size: 2em;
  margin: 0.5em 0;
}

#preview h2 {
  font-size: 1.5em;
  margin: 0.5em 0;
}

#preview h3 {
  font-size: 1.2em;
  margin: 0.5em 0;
}